.about {
  display: flex;
  padding: 10% 0;
}

html {
  scroll-behavior: smooth;
}

.leftProfile {
  width: 80%;
  text-align: center;
}

.leftProfile img {
  border-radius: 50%;
}

.right p {
  color: white;
}

.right {
  padding-top: 100px;
  padding-right: 50px;
}

@media screen and (max-width: 1040px) {
}

@media screen and (max-width: 640px) {
  .about {
    flex-direction: column;
  }
}
